.form-fields {
  display: flex;
  gap: 20px;
}

.form-field {
  font-size: 0.8rem;
  flex: 1;
}

.form-field span {
  display: block;
  margin-bottom: 5px;
}

.form-field-radio input {
  margin-right: 8px;
}


.ticket-header-title {
  font-size: 2.5rem;
  padding: 0 40px;
  text-align: center;
  margin:0;
  font-weight: 300;
  color: var(--text-light);
}

.ticket-header p {
  margin-top:0;
}

.ticket-header span {
  display: block;
}

.ticket-header-details {
  display: flex;
  gap:10px;
  align-items: center;
  background-color: var(--backgroud-color);
  padding: 20px;
}



.ticket-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.ticket-header-details ul {
  width: 100%;
}
.ticket-header-details li {
  padding: 5px 10px;
}




.ticket-column .ticket-container {
  height: 300px;
}

.ticket-list-user .ticket-container {
  padding: 0;;
}

.ticket-container {
  position: relative;
  padding: 0.5rem;
}

.ticket-content {
  margin-left: 90px;
}

.ticket-icon {
  position: absolute;
  top:8px;
  left:25px;
}

.ticket-title {
  font-weight: 400;
  font-size: 1.6rem;
}

.ticket-title a {
  text-decoration: none;
  color: var(--text-color);
}

.ticket-sub-header {
  color: #999;
}

.ticket-icon span {
  font-size: 4rem;
  color: var(--primary);
}

.ticket-category {
  background-color: var(--backgroud-color);
  padding: 4px 10px;
  border-radius: 20px;
  margin-right: 10px;
  color: var(--text-light);
}

.ticket-description {
  margin-bottom: 20px;
  color: #999;
  font-size: 0.9rem;
  line-height: 1.3rem;
}


.ticket-sub-header {
  display: flex;
  gap:15px;
  margin:20px 0;
  align-items: center;
  justify-content: start;
  font-size: 0.9rem;
}




.button {
  padding:5px 10px;
  border-radius: 50px;
  border:none;
  color: var(--text-light);
  cursor: pointer;
}

.button-secondary {
  background-color: var(--secondary);
  color: var(--text-color);
}

.button-icon {
  padding: 5px;
}


.ticket-action {
 margin-left: 20px;
 margin-top: 10px;
}

.ticket-action span {margin-right: 5px;}
.ticket-action p {margin: 0;}

.buttons {
  display: flex;
  gap:10px;
  margin-bottom: 10px;
}

h2 {
  margin:0;
}

.buttons-hide {
  display: none;
}



.filtros {
  display: flex;
  gap:10px;
  background-color:var(--secondary);
  padding: 12px 10px;
  margin:0;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  justify-content: center;
  border:var(--text-color) solid 1px;
  border-bottom: 0;
}

.filtros select {
  background-color: #fff;
  border:none;
  border-radius: 10px;
  padding: 10px;
  color: var(--text-color);
  border: #dba753 solid 1px;
  
}

.filtro-item label {
  margin-bottom: 5px;
}

.filtro-tags {
  display: flex;
  gap:10px;
  margin:0;
  flex-wrap: wrap;
}

.filtro-tags li:first-child {
  display: none;
}

.filtro-tags li button {
  padding: 10px 15px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
}

.filtro-tags li button:hover {
  background-color: #447f74;
  color: var(--text-light);
  transition: all ease 0.3s;
}
.filtro-tags li button.ativo {
 background-color:var(--primary);
 color: var(--text-light);
 font-weight: 500;
}

.sidebar-left, .sidebar-right {
  width: 200px;
}

.sidebar-right .menu,
.sidebar-left .menu {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}






.hero-single {
  background-color: var(--secondary);
  margin-bottom: 10px;
  padding: 5px 0;
  box-sizing: content-box;
}



.container {
  max-width: 1200px;
  margin:0 auto;
  display: flex;
  justify-content: space-evenly;
  padding: 75px 0;
}

.section-como-funciona,
.section-ultimos-pedidos {
  text-align: center;
}

.section-como-funciona .container,
.section-ultimos-pedidos .container {
  flex-direction: column;
  max-width: 980px;
}

.card-item .cat-transportes {color:#5E9DFC;}
.card-item .cat-animais {color:#3B9A7C;}
.card-item .cat-companhia {color:#FE9424;}
.card-item .cat-compras {color:#8292FD;}

.card-items {
  display: flex;
  justify-content: space-between;
  margin-top:50px;
  gap:20px;
}

.card-item {
  background-color: #fff;
  padding: 40px;
  border-radius: 25px;
  flex:1;
  border:var(--secondary) solid 1px;;
  transform: translateY(5px);
}

.card-item:hover {
  box-shadow: 0px 3px 0 rgba(0,0,0,0.1);
  transform: translateY(0px);
  transition: all .3s;
}

.card-item .card-number {
  font-size: 2.2rem;
  font-weight: 800;
  margin-bottom: 15px;
}

.card-item .card-title {
  font-size: 1.6rem;
  margin-bottom: 20px;
}

.card-item .card-description {
  font-weight: 100;
}

.card-item .card-category {
  background-color: var(--backgroud-color);
  padding: 6px 20px;
  border-radius: 25px;
  display: inline-block;
  font-size: 0.9rem;
  border:var(--secondary) solid 1px;
}

.card-item .card-location {
  font-size: 0.8rem;
}

.card-item span {
  font-size: 4rem;
  display: block;
  margin-bottom: 40px;
  color: var(--primary);
}

.section-header h3 {
font-size: 4rem;
font-weight: 500;
margin:0;
text-shadow: 3px 3px 0px var(--primary);
letter-spacing: 0.3rem;
}

.section-footer {
  margin-top: 60px;
  display: flex;
  justify-content: center;
}

.section-como-funciona .section-footer .btn {
  display: flex;
  align-items: center;
  gap:8px;
}
.section-como-funciona .section-footer .btn span {
  display: inline-block;
}

.section-ultimos-pedidos .section-header h3 {
  text-shadow: 3px 3px 0px var(--secondary);
}

.section-ultimos-pedidos .section-header p {
  color: var(--text-light);
}

.section-header p {
  font-size: 1.5rem;
  font-weight: 100;
}

.section-hero {
  background-color: var(--secondary);
}

.section-hero .container {
  gap:50px;
  justify-content: space-around;
  width: 1024px;
}

.section-ultimos-pedidos {
  background: var(--primary);
}

.section-ultimos-pedidos .card-item {
  border:#fff solid 1px;
  padding:40px 20px;
}
.section-ultimos-pedidos .card-item span {
  font-size: 3.5rem;
}
.section-ultimos-pedidos .card-item .card-title {
  font-size: 1.2rem;
}
.section-ultimos-pedidos .card-item .card-location {
  font-size: 0.9rem;
  margin:15px 0;
}

.hero-section-text {
  
  flex:2;
}
.hero-section-text h2 {
  font-size: 3.5rem;
  font-weight: 500;
  color:var(--backgroud-color);
  
}

.hero-section-text p {
  font-size: 1.8rem;
  font-weight: 300;
  margin-top: 20px;
  margin-bottom: 50px;
  color: var(--backgroud-color);
}
.hero-section-image {
  flex: 1.5;
  text-align: center;
}
.hero-section-image span {
  font-size: 15rem;
  color: var(--primary);
}

.btn {
  font-size: 1.4rem;
  padding: 12px 25px;
  color: var(--text-light);
  border:none;
  border-radius: 50px;
  cursor: pointer;
  font-weight: 300;
  letter-spacing: 0.1rem;
  background-color: var(--backgroud-color);
  transition: all 0.3s;
  transform-origin: center;
  line-height: 1;
  will-change: transform;
  text-decoration: none;
}

.btn-small {
  font-size:1rem;
  padding: 10px 15px;
  letter-spacing: 0;
  font-size: 0.8rem;
}

.btn-normal {
  padding: 10px 15px;
  font-size: 1rem;
}

.btn-primary {
  background-color: var(--primary);
  border:var(--primary) solid 1px;
}

.btn-primary:hover {
  background-color: #407c71;
  border:#407c71 solid 1px;
}

.btn-form-add {
  background-color:var(--primary);
  color: var(--secondary);
  border:var(--primary) solid 1px;
  font-weight:500;
  letter-spacing: 0.05rem;
}

.btn-secondary {
  border:var(--primary) solid 1px;
  background-color: transparent;
  color: var(--primary);
}

.btn-secondary:hover {
  background-color: var(--primary);
  color: var(--text-light);
}



footer {
  padding: 50px 0;
  margin-top: 50px;
}

footer p {
  margin:10px 0;
  text-align: center;
}

.contact-container {
  width: 500px;
  margin:0 auto;
}


.success-message {
  margin-top: 1rem;
  padding: 1rem;
  background-color: #e6ffed;
  border: 1px solid #a3e5b1;
  color: #1a7f3b;
  border-radius: 4px;
}

.link-detalhes {
  color: #1a7f3b;
  text-decoration: underline;
  margin-left: 0.5rem;
}

.wrapper-pedidos {
  margin-top: 30px;
}

/* navbar */
nav {
  display: flex;

}
nav a, nav button {
  margin-left: 10px;
}
nav button {
  background: #fff;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 6px 10px;
  border-radius: 4px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 1em;
}

/* auth forms */
form.signup, form.login {
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}

/* new workout form */
label, input {
  display: block;
}
input {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}



.single-ticket {
  background-color: #fff;
  padding: 2rem;
  border:var(--text-color) solid 1px;
